export const emailValidation = (email) => {
  let reg = /\S+@\S+\.\S+/;
  let x = reg.test(email);
  return x;
};

export const emptyValidation = (value, min) => {
  if (value.length >= min) {
    return true;
  } else {
    return false;
  }
};

export const replaceValue = (value, type) => {
  let string = /[^a-zA-Z]/g;
  let number = /[^0-9]/g;
  if (type === "string") return value.replace(string, "");
  else if (type === "number") return value.replace(number, "");
};
