<template>
  <Alert :arrayAlert="arrayAlert" v-on:changeShow="closeAlert($event)" />
  <div class="register-page">
    <div class="container-register" @keydown.enter="btnRegister">
      <h2 class="title-page">
        {{ getLocalizationtxt.userPage.registration }}
      </h2>
      <div class="row-input">
        <input
          type="text"
          v-model="name"
          @input="handlingFormInput($event, 'string', 'name', 'errorName', 2)"
          @paste="eventStop"
          class="input-form"
          :placeholder="getLocalizationtxt.userPage.name"
          maxlength="16"
          ref="inputName"
        />
        <p :class="errorName" class="error-input name" ref="textErrorName">
          {{ getLocalizationtxt.userPage.nameError }}
        </p>
      </div>
      <div class="row-input">
        <input
          type="text"
          v-model="phone"
          @input="handlingFormInput($event, 'number', 'phone', 'errorPhone', 5)"
          @paste="eventStop($event)"
          class="input-form"
          v-bind:placeholder="getLocalizationtxt.userPage.phone"
          maxlength="16"
        />
        <p :class="errorPhone" class="error-input phone">
          {{ getLocalizationtxt.userPage.phoneError }}
        </p>
      </div>
      <div class="row-input">
        <input
          type="email"
          v-model="email"
          @input="handlingFormInput($event, 'email', 'email', 'errorEmail', 5)"
          @paste="eventStop($event)"
          class="input-form"
          v-bind:placeholder="getLocalizationtxt.userPage.email"
        />
        <p :class="errorEmail" class="error-input email">
          {{ getLocalizationtxt.userPage.emailError }}
        </p>
      </div>
      <div class="row-input">
        <input
          type="password"
          v-model="password"
          class="input-form"
          @input="
            handlingFormInput(
              $event,
              'password',
              'password',
              'errorPassword',
              5
            )
          "
          maxlength="32"
          v-bind:placeholder="getLocalizationtxt.userPage.password"
          ref="inputPassword"
          @paste="eventStop($event)"
        />
        <img
          src="@/assets/icons/eye.svg"
          class="icon-eye"
          @click="changeInputType('inputPassword')"
        />
        <p :class="errorPassword" class="error-input password">
          {{ getLocalizationtxt.userPage.passwordError }}
        </p>
      </div>
      <div class="row-input">
        <input
          type="password"
          v-model="confirmPassword"
          class="input-form"
          @input="
            handlingFormInput(
              $event,
              'confirmPassword',
              'confirmPassword',
              'confirmPasswordError',
              5
            )
          "
          maxlength="32"
          v-bind:placeholder="getLocalizationtxt.userPage.confirmPassword"
          ref="inputConfirmPassword"
          @paste="eventStop($event)"
        />
        <img
          src="@/assets/icons/eye.svg"
          class="icon-eye"
          @click="changeInputType('inputConfirmPassword')"
        />
        <p :class="confirmPasswordError" class="error-input confirm-password">
          {{ getLocalizationtxt.userPage.confirmPasswordError }}
        </p>
      </div>
      <button class="btn-form blue" @click="btnRegister">
        {{ getLocalizationtxt.userPage.send }}
      </button>
    </div>
  </div>
</template>

<script>
import Alert from "@/components/Alert";
import {
  emptyValidation,
  emailValidation,
  replaceValue,
} from "@/plugins/Validation";

export default {
  name: "Register",
  components: { Alert },
  data() {
    return {
      name: "",
      errorName: "",
      phone: "",
      errorPhone: "",
      email: "",
      errorEmail: "",
      password: "",
      errorPassword: "",
      confirmPassword: "",
      confirmPasswordError: "",
      registerClicked: true,
      actionsEmailError: false,
      arrayAlert: {
        msg: "",
        msgType: "",
        show: false,
      },
    };
  },
  computed: {
    getLocalizationtxt() {
      return this.$store.getters["localizationInfo"];
    },
  },
  mounted() {
    this.setAutofocus();
  },
  methods: {
    setAutofocus() {
      this.$refs.inputName.focus();
    },
    handlingFormInput(event, typeValidation, vModel, errorName, minLength) {
      let value = event.target.value;
      if (typeValidation === "string" || typeValidation === "number") {
        let replacedValue = replaceValue(value, typeValidation);
        value = replacedValue;
        this[vModel] = replacedValue;
        let errorCheckedEmpty = emptyValidation(value, minLength);
        if (errorCheckedEmpty) this[errorName] = "true";
        else this[errorName] = "false";
      } else if (typeValidation === "email") {
        let errorCheckedValidation = emailValidation(this[vModel]);
        if (errorCheckedValidation) this[errorName] = "true";
        else this[errorName] = "false";
      } else if (typeValidation === "password") {
        let errorCheckedEmpty = emptyValidation(value, minLength);
        if (errorCheckedEmpty) this[errorName] = "true";
        else this[errorName] = "false";
      } else if (typeValidation === "confirmPassword") {
        let errorCheckedEmpty = emptyValidation(value, minLength);
        if (errorCheckedEmpty && this.confirmPassword === this.password)
          this[errorName] = "true";
        else this[errorName] = "false";
      }
    },
    eventStop(event) {
      event.preventDefault();
    },
    changeInputType(input) {
      if (this.$refs[input].type == "password") this.$refs[input].type = "text";
      else this.$refs[input].type = "password";
    },
    btnRegister() {
      if (this.registerClicked) {
        if (
          this.errorName === "true" &&
          this.errorPhone === "true" &&
          this.errorEmail === "true" &&
          this.errorPassword === "true" &&
          this.confirmPasswordError === "true"
        ) {
          const data = {
            id: null,
            name: this.name,
            phone: this.phone,
            email: this.email,
            password: this.password,
          };
          this.$store.dispatch("A_REGISTER_NEW_USER", data);
          this.checkStateEmailError();
          if (!this.actionsEmailError) {
            // this.$router.push("/login");

            // AUTOLOGIN
            this.$store.dispatch("A_AUTOLOGIN");
            this.$router.push("/persons");
          } else {
            this.arrayAlert.show = true;
            this.arrayAlert.msg = this.getLocalizationtxt.alertPage.error_email;
            this.arrayAlert.msgType = "error";
          }
          this.registerClicked = false;
          setTimeout(() => {
            this.registerClicked = true;
          }, 3000);
        } else if (
          this.errorName === "" ||
          this.errorPhone === "" ||
          this.errorEmail === "" ||
          this.errorPassword === "" ||
          this.confirmPasswordError === ""
        ) {
          this.arrayAlert.show = true;
          this.arrayAlert.msg = this.getLocalizationtxt.alertPage.empty_info;
          this.arrayAlert.msgType = "error";
        }
      }
    },
    checkStateEmailError() {
      this.actionsEmailError = this.$store.getters["checkStateEmailError"];
    },
    closeAlert(data) {
      this.arrayAlert.show = data;
      this.arrayAlert.msg = "";
    },
  },
};
</script>

<style lang="scss" scoped>
.register-page {
  transform: translate(-50%, -50%);
  position: absolute;
  top: 50%;
  left: 50%;

  .container-register {
    border: solid 1px #dee2e6;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px 35px;
    animation: openForm 0.5s;

    @keyframes openForm {
      0% {
        transform: translate(20%, 0px);
      }
      100% {
        transform: translate(0px, 0px);
      }
    }

    .title-page {
      margin-bottom: 20px;
    }

    .icon-eye {
      width: 15px;
      cursor: pointer;
      position: absolute;
      top: 7px;
      right: 5px;
    }
  }
}
</style>
